body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html , body , #root {
  height: 100%;
  margin: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hidden {

  display: none!important;
}

img , a , button {

  display: inline-block;
}

.error {

  background: rgb( 186 , 60 , 42 );
  padding: 1vh 1.5vh;
  border-radius: 9px;
  margin: 1vh 0;
  font-weight: 600;
  color: #fff;
  display: inline-block;
}

input {

  background-color: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  display: block;
  width: 85%;
  min-width: 250px;
  padding-bottom: 2.5vh;
  font-size: 1.5rem; 
}

button {

  cursor: pointer;
  background: transparent;
  border: 1px solid transparent;
  border-radius: 50px;
  padding: .5vh 3vh;
}

/*
* ul only semantical tag
*/
ul {

  list-style: none;
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 350px) , (max-height: 450px) {

  input {
    font-size: .85rem;
    border-width: 1px;
  }
}